<template>
    <div class="chat-window">
        <div class="chat-code" v-if="data.type == 1 || data.type == 2">
            <p v-if="data.msg">{{data.msg}}</p>
            <input type="text" class="chat-input" v-if="data.type == 2" v-model="keyword" placeholder="请输入抽奖口令">
            <div class="chat-confirm"><button @click="confirm">确认</button></div>
            <span class="chat-window-close" @click="close">×</span>
        </div>
        <div class="chat-guess" v-else-if="data.type == 3">
            <p>{{data.msg}}</p>
            <div class="chat-guess-options">
                <a href="javascript:;" :class="[selects.indexOf(index) >= 0 ? 'active' : '']" @click="selects.push(index)" v-for="(item,index) in data.selects">{{item.text}}</a>
            </div>
            <div class="chat-confirm"><button @click="confirm">确认</button></div>
            <span class="chat-window-close" @click="close">×</span>
         </div>
    </div>
</template>

<script>
export default {
    props:['data'],
    data(){
        return {
            keyword: null,
            selects: []
        }
    },
    methods:{
        close(){
            this.$emit('close')
        },
        confirm(){
            this.$emit('confirm',{
                keyword: this.keyword,
                selects: this.selects
            })
        }
    }
}
</script>




